.mainCanvas {
  z-index:100;
  position: absolute;
  left: 0;

  background-image: -webkit-gradient(linear, left bottom, right top, from(transparent), to(#00ff0821));

  background-image: -webkit-linear-gradient(bottom left, transparent, #00ff0821);

  background-image: -o-linear-gradient(bottom left, transparent, #00ff0821);

  background-image: linear-gradient(to top right, transparent, #00ff0821);
}

.backgroundCanvas {
  position:absolute;
  left: 0;
  background: black;
  background-image: -webkit-gradient(linear, left top, right bottom, from(black), to(#ff00ff26));
  background-image: -webkit-linear-gradient(top left, black, #ff00ff26);
  background-image: -o-linear-gradient(top left, black, #ff00ff26);
  background-image: linear-gradient(to bottom right, black, #ff00ff26);
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



